.change {
  display: inline-block;
  padding: 10px 50px; /* Adjust padding as needed for the button size */
  background-color: rgb(241, 185, 80); /* Green color */
  color: #fff; /* Text color */
  border: none;
  border-radius: 4px; /* Rounded corners */
  cursor: pointer;
  font-size: 24px; /* Adjust font size as needed */
  font-weight: 550;
  text-decoration: none; /* Remove underlines for links */
  text-align: center;
  transition: background-color 0.3s ease; /* Smooth color transition on hover */
  margin-bottom: 0px; /* Add more styles as needed */
}

/* Hover effect */
.change:hover {
  background-color: #e9a615; /* Darker green color on hover */
  color: #3f67d3;
}

.link {
  color: #fff;
  text-decoration: none; /* Remove underlines for links */
}
